.footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: #FFBF00;
    color: #784212;
    text-align: center;
  }
  .box{
    width: 400px;
   margin:0 auto;
   margin-top: 40px;
   box-shadow: 0px 2px #FFBF00;
   border: none;
   background-color: white;
  }
  .inp{
    border: none;
    
  }
  .pad{
    margin-right: 4px;
    position: relative;
  }

  @media (max-width:768px) {
    .box{
      width: 200px;
     margin:0 auto;
     margin-top: 40px;
     box-shadow: 0px 2px #FFBF00;
     border: none;
     background-color: white;
    }
  }
